.home-page {
    display: flex;
    flex-direction: column;
    justify-items: center;

    img {
        width: 40%;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }

    .home-text {
        font-weight: 900;
        font-size: 5.5em;
        font-family: MrGrieves-Regular;

        .letter {
            display: inline-block;
            line-height: 1em;
        }

        .text-wrapper {
            position: relative;
            display: inline-block;
            padding-top: 0.1em;
            padding-right: 0.05em;
            padding-bottom: 0.15em;
        }

        .line {
            opacity: 0;
            position: absolute;
            left: 0;
            height: 3px;
            width: 100%;
            background-color: black;
            transform-origin: 0 0;
        }

        .line1 { top: 0; }
        .line2 { bottom: 0; }
    }
}